.navbar {
  background: #660bf8;
  height: 60px;
  color: white;
}

.navbar a {
  color: white;
  font-size: 2rem;
  text-decoration: none;
  margin-right: 350px;
  /* line-height: 3; */
  position: relative;
  float: left;
  text-align: center;
}

.navinfo {
  color: white;
  font-size: 2rem;
  text-decoration: none;
  margin-right: 220px;
  /* line-height: 3; */
  float: left;
  text-align: center;
}

.navscore {
  color: white;
  font-size: 2rem;
  text-decoration: none;
  /* line-height: 3; */
  float: right;
  text-align: center;
}
.header {
  background: #5d5f5f;
  height: 130px;
}

.header h1 {
  margin-top: 60px;
  padding-top: 20px;
  text-align: center;
  color: white;
  font-size: 30px;
}

.header h2 {
  font-size: 20px;
  text-align: center;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color:  #660bf8;
  color: white;
  text-align: left;
  font-size: 10px;
}
.card {
    background: #fff;
    height: 170px;
    margin: 10px;
    position: relative;
    width: 255px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    float: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
  }
